import { slide_up } from './animations';
import { Component, OnInit, ViewChild } from '@angular/core';
import * as Rellax from 'rellax';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';
import { Observable } from 'rxjs';
import { DateRangeType, IgxCalendarComponent } from "igniteui-angular";
import { FormControl, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { cpuUsage } from 'process';
import { formatDate } from '@angular/common';
import { SHA3 } from 'crypto-js';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss'],
  animations:[
    slide_up
  ]
})
export class LandingComponent implements OnInit {
  data : Date = new Date();
  today = new Date(Date.now());
  focus;
  events = [];
  event_dict = {};
  checked = false;
  focus1;
  passValid = true;
  slide = true;
  bucket_list;
  fromdate;
  editing = false;
  editId = -1;
  dbObject: AngularFireObject<any>;
  dbObject2: AngularFireObject<any>;
  db_data;
  @ViewChild("calendar", { static: true }) public calendar: IgxCalendarComponent;
  public range = [];
  eventForm = new FormGroup({
    dateOfEvent: new FormControl('', [
      Validators.required,
    ]),
    title: new FormControl(''),
    description: new FormControl(''),
    img_link: new FormControl(''),
    password: new FormControl('',[
      Validators.required,
    ])
  });

  constructor(fb: FormBuilder, db: AngularFireDatabase, private f: FormBuilder, private modalService: NgbModal) {
    this.dbObject = db.object('/dates');
    this.dbObject.valueChanges()
      .subscribe(data => {
        this.db_data = Object.values(data);
        this.db_data = this.db_data.sort(function (a, b) {
          let x = new Date(a.date);
          let y = new Date(b.date);
          if (x > y) { return 1 };
          if (x < y) { return -1; };
          return 0;
        });
        this.db_data.reverse();

        let curr_dates: Array<Date> = [];
        this.event_dict = {};

        this.db_data.forEach((element: any, index) => {
            let d = new Date(element.date);
            curr_dates.push(d);
            if (this.event_dict[d.toDateString()] == null){
              this.event_dict[d.toDateString()] = [element];
            } else{
              this.event_dict[d.toDateString()].push(element);
            }
          });
        this.events = this.db_data;
        this.calendar.specialDates =
          [
            { type: DateRangeType.Specific, dateRange: curr_dates },
          ]
      });

    this.dbObject2 = db.object('/bucket-list')
    this.dbObject2.valueChanges()
      .subscribe(data => {
        this.bucket_list = data;
      })
    }

  ngOnInit() {
    var rellaxHeader = new Rellax('.rellax-header');
    var body = document.getElementsByTagName('body')[0];
    body.classList.add('login-page');
    var navbar = document.getElementsByTagName('nav')[0];
    navbar.classList.add('navbar-transparent');
    this.calendar.onSelection.subscribe((e, pt=this) => {
      pt.onSelect(e);
    })
  }

  ngOnDestroy(){
    var body = document.getElementsByTagName('body')[0];
    body.classList.remove('landing-page');
    var navbar = document.getElementsByTagName('nav')[0];
    navbar.classList.remove('navbar-transparent');
  }

  public submitPTOdays(dates: Date[]) {
    this.range = dates;
    this.calendar.specialDates =
      [{ type: DateRangeType.Specific, dateRange: this.range }];
    // console.log(this.calendar.specialDates);
    this.range.forEach((item) => {
      this.calendar.selectDate(item);
    });
  }

  open(content) {
    this.modalService.open(content);
    this.editing = false;
    this.eventForm.reset();
  }

  onSelect(dates: Date){
    // console.log(dates);
    this.events = this.event_dict[dates.toDateString()];
  }

  // edit(id, content) {
  //   this.editId = id;
  //   this.editing = true;
  //   let prev_data = this.db_data[this.events[id].index];
  //   let pd = new Date(prev_data.date);
  //   let d: any = document.getElementById("dateOfEvent")
  //   this.fromdate = formatDate(pd, 'yyyy-mm-dd', 'en');

  //   this.eventForm = this.f.group({
  //     dateOfEvent: [this.fromdate, [Validators.required]],
  //     title: [prev_data.title],
  //     description: [prev_data.description],
  //     img_link: [prev_data.img_link],
  //     password: ["", [Validators.required]]
  //   });
  //   this.modalService.open(content);
  // }

  resetevent(){
    this.events = this.db_data;
  }

  SaveBucklist(){
    let message: any = document.getElementById('buckval');
    if (message.value != ""){
      let list_elem = {
        content: message.value,
        done: false
      }
      this.bucket_list.push(list_elem);
      this.dbObject2.set(this.bucket_list);
    }
    message.value = "";
  }

  updateBuckList(i, event){
    this.bucket_list[i].done = event;
    this.dbObject2.set(this.bucket_list);
  }

  formHandler(){

    // console.log(this.eventForm.value)
    let val = this.eventForm.value;
    let date = new Date(val.dateOfEvent.year, val.dateOfEvent.month - 1, val.dateOfEvent.day);
    // console.log(date);
    let event = {
      date: date.toDateString(),
      title: val.title,
      description: val.description,
      img_link: val.img_link
    }
    let encrypted = SHA3(val.password);
    // console.log(encrypted.toString());
    if (encrypted.toString() === "cf94a37106d28ee7c61f5140df38a29748ef1dfe61508ad0b036f596c7403214016ed37808262b9d53461b03a7b521d371b4b05279344b32ddd6d77aec57f743"){
      this.db_data.push(event);
      this.dbObject.update(this.db_data);
    }
    this.modalService.dismissAll();
    this.eventForm.reset();
  }

  zeroPad(nr, base) {
    var len = (String(base).length - String(nr).length) + 1;
    return len > 0 ? new Array(len).join('0') + nr : nr;
  }

  onSlide($event){
    this.slide = !this.slide;
  }
}
